#Navigation-Bar{
    position: relative;
    min-height: 40px;
}

#res-menu{
    display: none;
}

@media screen and (max-width: 750px){
    .res-hide{
        display: none;
    }

    .active{
        animation: menu 0.5s;
        position: absolute;
        flex-direction: column;
        background: var(--primary-bg-color);
        top: 40px;
        left: 0;
        width: 100%;
        height: 100vh;
        z-index: 10;
    }

    #res-menu{
        display: block;
    }

}

@keyframes menu{
    0%{
        opacity: 0;
        transform: translateY(-100%);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}