/* Universal Reset */
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/* Body */
body{
    min-height: 100vh;
    background: var(--primary-bg-color);
    width: 100%;
}

/* Scroll */
::-webkit-scrollbar{
    width: 10px;
}

::-webkit-scrollbar-thumb{
    border-radius: var(--primary-border-radius);
    background: var(--success-color);
}

::-webkit-scrollbar-thumb:hover{
    background: var(--warning-color);
}

/* Flex */
.flex{
    display: flex;
}

.flex-col{
    flex-direction: column;
}

.x-center{
    justify-content: center;
}

.y-center{
    align-items: center;
}

.y-s-between{
    align-items: space-between;
}

.flex-center{
    justify-content: center;
    align-items: center;
}

.s-between{
    justify-content: space-between;
}

.s-around{
    justify-content: space-around;
}

.wrap{
    flex-wrap: wrap;
}

/* Gap */
.gap-p{
    gap: var(--primary-gap);
}

.gap-s{
    gap: var(--secondary-gap);
}

.gap-t{
    gap: var(--tertiary-gap);
}

/* Text */
.text-center{
    text-align: center;
}

.text-right{
    text-align: right;
}

.text-left{
    text-align: left;
}

.text-justify{
    text-align: justify;
}

.text-d-none{
    text-decoration: none;
}

/* List */
.list-d-none{
    list-style: none;
}

/* Font */
.font-p{
    font-family: var(--primary-font);
}

.font-s{
    font-family: var(--secondary-font);
}

.font-fancy{
    font-family: var(--fancy-font);
}

/* Font Size */
.fs-p{
    font-size: var(--primary-font-size);
}

.fs-s{
    font-size: var(--secondary-font-size);
}

.fs-t{
    font-size: var(--tertiary-font-size);
}

/* Font Weight */
.fw-p{
    font-weight: var(--primary-font-weight);
}

.fw-s{
    font-weight: var(--secondary-font-weight);
}


/* Font Color */
.fc-p{
    color: var(--primary-font-color);
}

.fc-s{
    color: var(--secondary-font-color);
}

.fc-t{
    color: var(--tertiary-font-color);
}

/* Background Color */
.bg-p{
    background-color: var(--primary-bg-color);
}

.bg-s{
    background-color: var(--secondary-bg-color);
}

.full-bg{
    background: var(--full-bg-color);
}

/* Width */
.w-fluid{
    width: 100%;
}

.w-large{
    width: 75%;
}

.w-mid{
    width: 50%;
}

.w-small{
    width: 25%;
}

.w-200{
    width: 200px;
}

/* Height */
.h-fluid-v{
    height: 100vh;
}

.h-fluid{
    height: 100%;
}

.h-large{
    height: 75%;
}

.h-mid{
    height: 50%;
}

.h-small{
    height: 25%;
}

/* Border */
.bsize-p{
    border: var(--primary-border-size);
}

.bsize-s{
    border: var(--secondary-border-size);
}

/* Border Color */
.bc-p{
    border-color: var(--primary-border-color);
}

.bc-s{
    border-color: var(--secondary-border-color);
}

.bc-t{
    border-color: transparent;
}

/* Border Radius */
.br-p{
    border-radius: var(--primary-border-radius);
}

.br-s{
    border-radius: var(--secondary-border-radius);
}

/* Padding */
.p-tb-p{
    padding: var(--primary-padding) 0;
}

.p-tb-s{
    padding: var(--secondary-padding) 0;
}

.p-lr-p{
    padding: 0 var(--primary-padding);
}

.p-lr-s{
    padding: 0 var(--secondary-padding);
}

.p-p{
    padding: var(--primary-padding);
}

.p-s{
    padding: var(--secondary-padding);
}

/* Margin */
.m-0{
    margin: 0;
}

.m-tb-p{
    margin: var(--primary-margin) 0;
}

.m-tb-s{
    margin: var(--secondary-margin) 0;
}

.m-lr-p{
    margin: 0 var(--primary-margin);
}

.m-lr-s{
    margin: 0 var(--secondary-margin);
}

.m-l-t{
    margin-left: var(--t-margin);
}

.m-p{
    margin: var(--primary-margin);
}

.m-s{
    margin: var(--secondary-margin);
}

.m-auto{
    margin: auto;
}

/* Position */
.pos-rel{
    position: relative;
}

.pos-abs{
    position: absolute;
}

/* Opacity */
.opacity-p{
    opacity: var(--primary-opacity);
}

.opacity-s{
    opacity: var(--secondary-opacity);
}

/* Messages */
.success{
    background: var(--success-color);
}

.error{
    background: var(--error-color);
}

.success-text{
    color: var(--success-color);
}

.error-text{
    color: var(--error-color);
}

.warning-text{
    color: var(--warning-color);
}

/* Float */
.left{
    left: 0;
    border-radius: 0 0 var(--primary-border-radius) 0;
}

.right{
    right: 0;
    border-radius: 0 0 0 var(--primary-border-radius);
}


/* Text Transform */
.uppercase{
    text-transform: uppercase;
}
