:root{
    --primary-bg-color: null;
    --secondary-bg-color: null;

    --primary-font-color: null;
    --secondary-font-color: null;
    --tertiary-font-color: null;

    --primary-border-color: null;
    --secondary-border-color: null;

    --success-color: #00fc79;
    --warning-color: #ff9800;
    --error-color: #f44336;
}

.dark{
    --primary-bg-color: #232323;
    --secondary-bg-color: #4f4f4f;
    --full-bg-color: #111111;

    --primary-font-color: #ffffff;
    --secondary-font-color: #f2f2f2;
    --tertiary-font-color: #bdbdbd;

    --primary-border-color: #4f4f4f;
    --secondary-border-color: #bdbdbd;

    --primary-button-color: #3d3d3d;
    --secondary-button-color: #5f5f5f;
}

.light{
    --primary-bg-color: #ffffff;
    --secondary-bg-color: #f2f2f2;
    --full-bg-color: #959cff;

    --primary-font-color: #0000FF;
    --secondary-font-color: #ffffff;
    --tertiary-font-color: #232323;

    --primary-border-color: #e0e0e0;
    --secondary-border-color: #bdbdbd;

    --primary-button-color: #fc0079;
    --secondary-button-color: #929090;
}