.p-image{
    position: relative;
    width: 300px;
    height: 300px;
    overflow: hidden;
    border-radius: 50%;
}

.p-image:before{
    content: "Ahmad Jr Software Engineer";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    color: var(--secondary-font-color);
    font-size: var(--primary-font-size);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    opacity: 0;
    transition: all 0.3s ease;
}

.p-image:hover:before{
    opacity: 1;
}

.p-image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}


@media screen and (max-width: 500px) {
    .p-image{
        width: 250px;
        height: 250px;
    }
}

@media screen and (max-width: 400px) {
    .p-image{
        width: 200px;
        height: 200px;
    }
}
