.container{
    margin: 1rem auto;
    max-width: 1200px;
    width: 100%;
    border-radius: var(--primary-border-radius);
    padding: 2rem;
}


.card{
    max-width: 350px;
    padding: var(--secondary-padding);
    width: 100%;
    border-radius: var(--secondary-border-radius);
    background: var(--secondary-bg-color);
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);
}

.card:hover{
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.4);
}

.summary{
    position: relative;
    width: 100%;
    height: 30px;
    overflow: hidden;
}

/* Inputs */
.input{
    width: 100%;
    padding: 10px;
    border: var(--primary-border-size) solid var(--primary-border-color);
    outline: none;
    border-radius: 5px;
}

.label{
    cursor: pointer;
}

.input:focus{
    border-color: var(--secondary-border-color);
}

/* Checkbox */
.checkbox{
    width: 15px;
    height: 15px;
    border: var(--primary-border-size) solid var(--primary-border-color);
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.2);
}

/* Selection */
.user-s-none{
    user-select: none;
}

/* Cursors */
.cursor-p{
    cursor: pointer;
}

/* Buttons */
.btn-primary{
    width: 100%;
    padding: 10px;
    background: var(--primary-button-color);
    color: var(--primary-font-color);
    border: none;
    outline: none;
    border-radius: var(--primary-border-radius);
    cursor: pointer;
}

.btn-primary:hover{
    background: var(--secondary-button-color);
}

.m-close-btn{
    position: absolute;
    font-size: 1.3rem;
    top: 0;
    padding: 5px 10px;
    background: var(--primary-button-color);
    color: var(--secondary-font-color);
    border: none;
    outline: none;
    cursor: pointer;
}

.btn-foot{
    min-width: 200px;
    background: var(--primary-button-color);
    color: var(--secondary-font-color);
    border-radius: var(--primary-border-radius);
    padding: 0.4rem 0rem;
}

.btn-action{
    background: var(--warning-color);
    color: var(--secondary-font-color);
    cursor: pointer;
    font-family: var(--secondary-font);
    font-size: var(--primary-font-size);
    outline: none;
    border: var(--primary-border-size) solid transparent;
    border-radius: var(--primary-border-radius);
    min-width: 200px;
    padding: 0.5rem 1rem;
    transition: all 0.5s;
}

.btn-action:hover{
    color: var(--warning-color);
    background: var(--secondary-font-color);
    border: var(--primary-border-size) solid var(--warning-color);
}


/* Message */
.message{
    animation: message 1s;
    position: relative;
    text-align: center;
    margin: 10px 0;
    width: 100%;
    min-height: 40px;
    border-radius: var(--primary-border-radius);
}

/* Hover Effects */
.hover-line{
    position: relative;
}

.hover-line::after{
    content: '';
    position: absolute;
    width: 0;
    height: 3px;
    background: var(--error-color);
    bottom: -5px;
    border-radius: var(--primary-border-radius);
    left: 0;
    transition: width 0.5s;
}

.hover-line:hover::after{
    width: 100%;
}

.hover-opacity{
    transition: opacity 0.5s;
}

.hover-opacity:hover{
    opacity: 0.6;
}

.progress{
    position: relative;
    width: 100%;
    height: 10px;
    background: var(--warning-color);
    border-radius: var(--primary-border-radius);
    opacity: 0.6;
    animation: progress 2s;
}

.progress-bar{
    position: absolute;
    height: 100%;
    background: var(--success-color);
    border-radius: var(--primary-border-radius);
}

.progress:hover .progress-bar{
    animation: progress 1s;
}

/* Animation */
@keyframes message{
    0%{
        opacity: 0;
        transform: translateY(-10px);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes progress{
    0%{
        width: 0;
    }
}

@media screen and (max-width: 400px) {
    .card{
        max-width: 100%;
        width: 100%;
    }

    .container{
        padding: 0.3rem;
    }
}