.home{
    display: grid;
    justify-content: space-around;
    align-items: center;
    gap: var(--tertiary-gap);
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}


.blink{
    animation: 0.3s blink linear infinite;
}


@media screen and (max-width: 768px) {
    .home{
        grid-template-columns: 1fr;
    }

    .text-c-media{
        text-align: center;
    }
}

@keyframes blink {
    0%{
        opacity: 0;
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}