/* Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Poppins&family=Roboto&display=swap');

/* Big Devices */
:root{
    /* Gap */
    --primary-gap: 0.7rem;
    --secondary-gap: 0.8rem;
    --tertiary-gap: 1rem;

    /* Font */
    --primary-font: 'Poppins', sans-serif;
    --secondary-font: 'Roboto', sans-serif;
    --fancy-font: 'Lobster', cursive;

    /* Font Size */
    --primary-font-size: 1rem;
    --secondary-font-size: 1.2rem;
    --tertiary-font-size: 1.5rem;

    /* Font Weight */
    --primary-font-weight: 400;
    --secondary-font-weight: 500;

    /* Border Sizes */
    --primary-border-size: 0.1rem;
    --secondary-border-size: 0.2rem;

    /* Border Radius */
    --primary-border-radius: 0.4rem;
    --secondary-border-radius: 0.8rem;

    /* Padding */
    --primary-padding: 0.3rem;
    --secondary-padding: 0.5rem;

    /* Margin */
    --primary-margin: 1rem;
    --secondary-margin: 1.2rem;

    /* Opacity */
    --primary-opacity: 0.4;
    --secondary-opacity: 0.8;
    --full-opacity: 1;
}

/* Small Devices */
@media screen and (max-width: 768px){
    :root{
        /* Gap */
        --primary-gap: 0.5rem;
        --secondary-gap: 0.7rem;
        --tertiary-gap: 0.8rem;

        /* Font Size */
        --primary-font-size: 0.8rem;
        --secondary-font-size: 1rem;
        --tertiary-font-size: 1.2rem;

        /* Border Sizes */
        --primary-border-size: 0.08rem;
        --secondary-border-size: 0.1rem;

        /* Border Radius */
        --primary-border-radius: 0.3rem;
        --secondary-border-radius: 0.6rem;

        /* Padding */
        --primary-padding: 0.8rem;
        --secondary-padding: 1rem;

        /* Margin */
        --primary-margin: 0.8rem;
        --secondary-margin: 1rem;
        --t-margin: 2rem;
    }
}